<template>
  

    <el-container style="height: 900px; border: 1px solid #eee">
      <el-aside width="200px" style="background-color: rgb(238, 241, 246)">
        <el-menu :default-openeds="['1', '3']">
          <el-submenu index="1">
            <template slot="title"><i class="el-icon-message"></i>作业1</template>
            <el-menu-item-group>
              <el-menu-item index="1-2"><router-link to="/homework1daily">容量预测模型</router-link></el-menu-item>
            </el-menu-item-group>
            
          </el-submenu>
          <el-submenu index="2">
            <template slot="title"><i class="el-icon-menu"></i>作业2</template>
            <el-menu-item-group>
              <!-- <template slot="title">分组一</template> -->
              <el-menu-item index="2-1"><router-link to="/homework2hourly">监控指标动态阈值告警</router-link></el-menu-item>
            </el-menu-item-group>
          </el-submenu>
        </el-menu>
      </el-aside>
      
      <el-container>
        <el-header style="text-align: right; font-size: 12px">
          
          <span>第三组作业</span>
        </el-header>
        
        <el-main>
        
        <p>容量预测告警模型</p>
        <!-- <p>{{ this.selectfwq }}</p> -->
        <!-- <p>{{ this.datefromto }}</p> -->
        <div class="block">
          <el-select v-model="selectfwq" placeholder="请选择服务器">
                <el-option
                  v-for="item in hw1_options"
                  :key="item.fwqID"
                  :label="item.label"
                  :value="item.fwqID">
                </el-option>
            </el-select>
            <span class="demonstration">&nbsp;&nbsp;</span>
            <el-date-picker
              v-model="datefromto"
              type="daterange"
              value-fromat = "yyyy-MM-dd"
              range-separator="至"
              start-placeholder="开始日期"
              end-placeholder="结束日期">
        </el-date-picker>
        <span class="demonstration">&nbsp;&nbsp;<p>"间隔几天预测/显示"</p></span>
        <el-input-number v-model="input_period" @change="handleChange" :min="1" :max="30" label="间隔几天预测/显示"></el-input-number>
      </div>
        
        <div id="main" style="width: 1400px; height: 700px"></div>
    
    
        </el-main>
      </el-container>
    </el-container>
    </template>
    
    <style>
      a {
        text-decoration: none;
      }
    
      .el-header {
        background-color: #B3C0D1;
        color: #333;
        line-height: 60px;
      }
      
      .el-aside {
        color: #333;
      }
    </style>
    
    <script>
    import api from "../../api/index";
    import axios from "axios"
    import querystring from "querystring"

    export default {
        data() {
            return{

                datefromto: '',

                hw1_options: [
                {
                    fwqID: '1',
                    label: 'test-aiops-hbase-203'
                    }, {
                      fwqID: '2',
                      label: 'test-bpfp-bs-engine2-ft'
                    }, {
                      fwqID: '3',
                      label: 'test-rr_offline_qf_cluster'
                    }
                  ],
                  
                selectfwq:'1',
                input_period:'',
                //时间数据存放
                
                start_timestamp : 0,
                end_timstamp: 0,


                //后台返回的数据
                list: [],
                datalist_used:[],
                datalist_predict:[]
            }
        },

        methods:{

          convertDateFormat(dateString){
            var date = new Date(dateString);
            var year = date.getFullYear();
            var month = date.getMonth() + 1;
            var day = date.getDate();
            

            if(month<10){
              month = '0' + month;
            }
            if(day<10){
              day = '0' + day;
            }

            return year + '/' + month + '/' + day;
          },

            drawChart(datalist_used,datalist_predict) {
          
            // 基于准备好的dom，初始化echarts实例  这个和上面的main对应
            let myChart = this.$echarts.init(document.getElementById("main"));
            
            const mergedXdata = [...datalist_used.map((item) => item.collect_time),...datalist_predict.map((item) => item.predict_time)];
            const mergedYdata = [...datalist_used.map((item) => item.used_value),...datalist_predict.map((item) => item.predict_value)];

            // 告警数据显示
            const abnormalXvalues = [];
            const abnormalYvalues = [];
             
            datalist_predict.map(function(item){
              if(item.isAlarm){
                abnormalXvalues.push(item.predict_time);
                abnormalYvalues.push(item.predict_value);
              }
            })
            // datalist_predict.map(function(item){
            //   if(item.isAlarm){
            //     abnormalYvalues.push(item.predict_value)
            //   }
            // })

            let markData = [{
              coord:[abnormalXvalues[8],abnormalXvalues[8]],
              symbol:'arrow',
              symbolSize:20,
              itemStyle:{
                color: 'yellow'
              }
            }]
            
            console.log(abnormalXvalues);
            console.log(abnormalYvalues)
            
            // 指定图表的配置项和数据
            let option = {
                xAxis:{
                  data:mergedXdata
                },
                yAxis:{
                  type:'value'
                },
                series:[{
                  data:mergedYdata,
                  itemStyle:{
                    normal:{
                      color:function(params){
                        const xValue = option.xAxis.data[params.dataIndex];
                        const timestamp = parseInt(Date.parse(xValue));
                        const threshold = 1680192000000;
                        // console.log(timestamp)
                        if(timestamp < threshold){
                          return 'green';
                        }else{
                          return 'red';
                        }
                      },  
                    }  
                  },
                  lineStyle:{
                    color:'#775281'
                  },
                  type:'line',
                  symbol:'rect',
                  showAllSymbol:true
                }],
                markPoint:{
                  data: [{
                    coord:[abnormalXvalues[8], abnormalYvalues[8]],
                    symbol:'arrow',
                    symbolSize:20,
                    itemStyle:{
                    color: 'yellow'
                    }
                  }]
                }
                
            };
              // 使用刚指定的配置项和数据显示图表。
              myChart.setOption(option);
            }
        },
        mounted(){

            axios.get('http://127.0.0.1:8090/static', {
              params:{
                serverID:1,
                period:1,
                startDate:'2022/6/14',
                endDate:'2023/12/22'
              }
            }).then(res =>{
            console.log(res.data);
            
              this.datalist_used = Array.from(res.data.list_used);
              this.datalist_predict = Array.from(res.data.list_predict);
              console.log(this.datalist_predict.map((item) => item.isAlarm));
              this.drawChart(this.datalist_used, this.datalist_predict);
              // this.drawChart(this.datalist_predict)
            })
            
         },

        updated(){
            this.datefromto[0] = this.datefromto[0].toString();
            this.datefromto[1] = this.datefromto[1].toString();

            axios.get('http://127.0.0.1:8090/static',{
              params:{
                serverID:this.selectfwq,
                period:this.input_period,
                startDate:this.convertDateFormat(this.datefromto[0]),
                endDate:this.convertDateFormat(this.datefromto[1])
              }
            }).then(res =>{
            
              this.datalist_used = Array.from(res.data.list_used);
              this.datalist_predict = Array.from(res.data.list_predict);
              console.log(this.datalist_predict.map((item) => item.isAlarm));
              this.drawChart(this.datalist_used, this.datalist_predict);
              // this.drawChart(this.datalist_predict)
            })
        }

    }
  


            
    </script>



// <!-- 对于作业一(需要按照天和月分别给）：
// list_used: 格式：[{...},{...}...{...}] 每个{...}内部：{collect_time:xxx, used_value:xxx, isAlarm:true/false}    
// list_predict: 格式：[{...},{...}...{...}] 每个{...}内部：{predict_time:xxx, predict_value:xxx, isAlarm:true/false} 
// 预测周期：  2023.4-2023.12
// 数据量：按天的话8*30差不多
//         按月的话8-9个

// 对于作业二：
// list_predict: 格式：[{...},{...}...{...}] 每个{...}内部：{predict_time:xxx, predict_value:xxx, max_value, min_value, isAlarm:true/false}
// 预测周期：2023.4.22-4.29 一周差不多
// 数据量：每小时一个，大概7*24 -->
